// React imports
import { useEffect, useRef, useState } from 'react';

// App imports
import { Wrapper } from '../wrapper';

// Context imports
import { useMapbox } from 'context/mapbox';

// Third-party imports
import Map from 'ol/Map';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import TileWMS from 'ol/source/TileWMS';
import OSM from 'ol/source/OSM';
import { fromLonLat, useGeographic } from 'ol/proj';
import * as easing from 'ol/easing';

const geoserverUrl = "https://geonode.ine.gov.py";

export const Mask = ({ map }: any) => {
  useGeographic();
  const mapRef = useRef(null);

  const { viewport } = useMapbox();
  const { latitude, longitude, zoom } = viewport;
  
  useEffect(() => {
    const currentMap: any = mapRef.current;
    
    const initialMap: any = new Map({
      target: currentMap,
      layers: [
        new TileLayer({
          source: new OSM(),
        }),
      ],
      view: new View({
        center: [longitude, latitude],
        zoom: zoom,
      }),
      controls: [],
    });

    return () => initialMap.setTarget(null);
  }, [latitude, longitude, zoom]);

  useEffect(() => {
   if (map) {
     // Append WMS layer once the map is fully loaded
     appendWMSLayer({ LAYERS: 'geonode:Distritos_Paraguay_INE_2022', SRS: 'EPSG:4326' });
     // appendWMSLayer({ LAYERS: 'geonode:BARLOC_PARAGUAY_2024', SRS: 'EPSG:4326' });
     // appendWMSLayer({ LAYERS: 'geonode:Departamentos_Paraguay_INE_20222', SRS: 'EPSG:4326' });
     // appendWMSLayer({ LAYERS: 'geonode:Manzanas_Paraguay_INE_20221', SRS: 'EPSG:4326' });
     // appendWMSLayer({ LAYERS: 'geonode:Hidrografia', SRS: 'EPSG:4326' });
     // appendWMSLayer({ LAYERS: 'geonode:Unidad_hidrografica', SRS: 'EPSG:4326' });
     // appendWMSLayer({ LAYERS: 'geonode:V_as_Paraguay_INE_20221', SRS: 'EPSG:4326' });
     // appendWMSLayer({ LAYERS: 'geonode:Vias_Principales_2022', SRS: 'EPSG:4326' });
   }
 }, [map]);

  // Function to add WMS layer
  const appendWMSLayer = ({ LAYERS, SRS }: any) => {
  if (map) {
    const wmsLayer = new TileLayer({
      source: new TileWMS({
        url: `${geoserverUrl}/geoserver/ows`,
        params: {
          SERVICE: 'WMS',
          VERSION: '1.3.0',
          FORMAT: 'image/png',
          TRANSPARENT: 'true',
          LAYERS,
          SRS,
        },
        serverType: 'geoserver',
      }),
      opacity: 0.7,
    });

    map.addLayer(wmsLayer);
  }
};

  return (
    <Wrapper>
      <div ref={mapRef} className="ol-map-wrapper">
      </div>
    </Wrapper>
  );
};

Mask.displayName="Mask";